* {
  font-family: Lato, serif;
}

.button-active {
  cursor: pointer;
}

.button-active:hover {
  background-color: #f13d7f;
}

.button-active:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.4));
}

.button-pressed {
  cursor: pointer;
}

.button-pressed:hover {
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.3);
}

.button-pressed:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.1));
}

.button-pressed-wtheff,
.footer__copy-wrap-icon-item:active {
  cursor: pointer;
}

.button-pressed-wtheff:active,
.footer__copy-wrap-icon-item:active {
  position: relative;
  top: 0.5px;
}

.col2 {
  width: calc(16.66667% - 30px);
}

.col5 {
  width: calc(41.66667% - 30px);
}

.col3 {
  width: calc(25% - 30px);
}

.col4 {
  width: calc(33.33333% - 30px);
}

.col1 {
  width: calc(8.33333% - 30px);
}

.margin-top {
  margin-top: 50px;
}

.even,
.odd {
  color: #222222;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.sprite {
  background: url(../img/spritesheet.png) no-repeat;
  display: block;
}

.sprite-Forma-1 {
  width: 32px;
  height: 29px;
  background-position: -5px -5px;
}

.sprite-Forma-1-_1_ {
  width: 40px;
  height: 40px;
  background-position: -5px -44px;
}

.sprite-Forma-1-_2_ {
  width: 47px;
  height: 35px;
  background-position: -5px -94px;
}

.sprite-Forma-1-_3_ {
  width: 45px;
  height: 32px;
  background-position: -5px -139px;
}

.sprite-Forma-1-copy {
  width: 23px;
  height: 22px;
  background-position: -5px -181px;
}

.footer__content {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  margin-top: 110px;
}

.footer__content-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.footer__about {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66667% - 30px);
  color: #898989;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.footer__company {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(16.66667% - 30px);
}

.footer__company h1 {
  color: #f16d7f;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
}

.footer__company a {
  color: #898989;
  font-size: 16px;
  font-weight: 400;
  line-height: 59px;
  text-align: right;
  text-decoration: none;
}

.footer__company a:hover {
  color: #ef5b70;
}

.footer__copy {
  background-color: #e6e6e6;
  height: 80px;
  margin-top: 80px;
}

.footer__copy-wrap {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  height: 100%;
}

.footer__copy-wrap-text {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  color: #7a7a7a;
  font-size: 16px;
  font-weight: 600;
  line-height: 42px;
}

.footer__copy-wrap-icon {
  display: flex;
}

.footer__copy-wrap-icon-item {
  height: 30px;
  width: 30px;
  margin-right: 5px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #b2b2b2;
  font-size: 16px;
  font-weight: 400;
  line-height: 42px;
  cursor: pointer;
}

.footer__copy-wrap-icon-item:hover {
  background-color: #f16d7f;
  color: #f7f7f8;
}

@media screen and (max-width: 960px) {
  .footer__content {
    padding-left: 20px;
    padding-right: 20px;
  }

  .footer__copy-wrap {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 780px) {
  .footer__content {
    padding-left: 15px;
    padding-right: 15px;
  }

  .footer__copy-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .footer__content {
    padding-left: 10px;
    padding-right: 10px;
  }

  .footer__copy-wrap {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 420px) {
  .footer__content {
    padding-left: 10px;
    padding-right: 10px;
  }

  .footer__copy-wrap {
    padding-left: 10px;
    padding-right: 10px;
  }
}